.post-container {
    width: 95%;
    display: grid;
    grid-template-columns: 50px 100px 1fr 30px;
    margin: auto;
    padding: 5px 0;
    flex-direction: column-reverse;
    
    .post-image {
        border-radius: 10px;
        background-size: contain;
        max-height: 100px;
        width: auto;
    }

    .user-photo {
        grid-column: 1;
        align-self: center;
        justify-self: center;
        
        img {
            width: 35px;
            border-radius: 50%;
        }
    }

    .user-name{
        grid-column: 2;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 17px;
        color: #3B3210;
        align-self: center;
        justify-self: center;
    }

    .post {
        grid-column: 3;
        height: auto;
        background: rgba(241, 200, 68, 0.2);
        border-radius: 25px;
        padding: 10px 20px;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 20px;
        color: #000000;
        font-family: 'Catamaran';
        word-break: break-all;

        .post-image{
            display: block;
            margin: auto;
            img{
                max-height: 200px;
            }
        }

        .post-tags {
            box-sizing: border-box;
            font-family: 'Catamaran';
            color: #3B3210;

            span {
                background: rgb(253, 235, 176);
                border: 1px solid #F1C844;
                border-radius: 10%;
                display: inline-block;
                padding: 2px 5px;
                margin-right: 5px;
                margin-top: 5px;
                &:nth-last-child(1) {
                    margin-right: 0px;
                }
            }
        }
    }
    
    .remove{
        grid-column: 4;
        width: 20px;
        justify-self: center;
        align-self: center;
        margin-left: 10px;
        cursor: pointer;
    }
}
