.feed-wall-container {
    margin: auto;
    width: 50%;
    height: 427px;
    background: #FFFFFF;
    box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.25);
    border-radius: 25px;

    .empty {
        margin: 15px 20px;
        text-align: center;
    }

    .scrollable{
        overflow: auto;
        width: 100%;
        height: calc(100% - 67px);
        &::-webkit-scrollbar{
            width: 1px;
        }
        &::-webkit-scrollbar-track {
            box-shadow: none
        }
        &::-webkit-scrollbar-thumb {
            outline: none;
        }
    }
    .header {
        font-family: 'Montserrat Subrayada';
        font-style: normal;
        font-weight: normal;
        font-size: 30px;
        line-height: 37px;
        color: #3B3210;
        margin: 15px 20px;
        background: #FFFFFF;
    }
}