.create-container {
    height: auto;
    padding-bottom: 10px;
    width: 25%;
    margin: auto;
    background: #FFFFFF;
    box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.25);
    border-radius: 25px;
    position: relative;

    .header {
        font-style: normal;
        font-weight: normal;
        font-size: 30px;
        line-height: 37px;
        color: #3B3210;
        margin: 5px 30px;
        margin-top: 20px;

    }

    .note {
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 15px;
        color: #3B3210;
        margin: 0 30px;
    }

    .validation-message {
        color: rgb(190, 39, 39);
    }

    .post-input {
        display: block;
        width: 82%;
        min-height: 20vh;
        background: rgba(241, 200, 68, 0.2);
        border: none;
        margin: auto;
        resize: none;
        font-family: 'Catamaran';
        font-size: 16px;
        line-height: 26px;
        color: #000000;
        margin-top: 5px;
        outline: none;
        padding-left: 10px;
        margin-bottom: 25px;
        cursor: text;
    }

    p {
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 15px;
        color: #3B3210;
        margin: 5px 30px;
    }

    .tag-input {
        display: block;
        width: 82%;
        border: none;
        margin: auto;
        font-size: 16px;
        line-height: 26px;
        color: #000000;
        outline: none;
        background: rgba(241, 200, 68, 0.2);
        padding-left: 10px;
        font-family: 'Catamaran';
        cursor: text;
    }

    .upload-input {
        display: none;
    }

    .btn-upload {
        font-family: Montserrat Subrayada;
        font-size: 16px;
        line-height: 20px;
        text-align: center;
        color: #3B3210;
        background: #FDF2CF;
        border: 1px solid #F1C844;
        box-sizing: border-box;
        border-radius: 25px;
        box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.3);
        transform:translateY(-2px);
        margin-top: 10px;
        margin-left: 30px;
        padding: 5px 10px;
        cursor: pointer;
        outline: none;
        &:hover{
            box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.25);
            transform:translateY(0);
        }
    }

    .btn-discard {
        margin-right: 15px;
        margin-left: 30px;
        background: #FFFFFF;
        border-radius: 25px;
        font-family: Montserrat Subrayada;
        font-size: 18px;
        line-height: 27px;
        text-align: center;
        color: #3B3210;
        border: 3px solid #FCD147;
        box-sizing: border-box;
        border-radius: 25px;
        padding: 5px 10px;
        cursor: pointer;
        outline: none;
        box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.3);
        transform:translateY(-2px);
        &:hover{
            box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.25);
            transform:translateY(0);
        }

    }
    .btn-create {
        float: right;
        margin-right: 30px;
        box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.3);
        transform:translateY(-2px);
        font-family: Montserrat Subrayada;
        font-size: 18px;
        line-height: 27px;
        text-align: center;
        color: #3B3210;
        background: #FCD147;
        border: 1px solid #F1C844;
        box-sizing: border-box;
        border-radius: 25px;
        padding: 6px 20px;
        cursor: pointer;
        outline: none;
        
        &:disabled {
            opacity: 0.5;

            &:hover{
                box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.3);
                transform: translateY(-2px);
            }
        }

        &:hover{
            box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.25);
            transform:translateY(0);
        }
    }
}