.header-container {
    display: grid;
    grid-template-columns: repeat(4, 25%);
    margin-bottom: 20px;
    min-height: 110px;
    position: relative;

    .banana {
        grid-column: 1/3;
        font-size: 40px;
        line-height: 49px;
        margin-top: 4vh;
        margin-left: 6.5vw;
        .banana-logo {
            width: 50px;
            vertical-align: super;
        }
    }

    .header-title {
        grid-column: 3/5;
        justify-self: right;
        margin-right: 6.5vw;
        margin-top: 4vh;
        font-size: 30px;

        .user-picture {
            border-radius: 50%;
            margin-left: 5vw;
            vertical-align: text-bottom;
        }
    }
}